import React, { Fragment, useEffect, useState } from "react";
import { Select, Form } from "react-bootstrap";
import DataTable from "react-data-table-component";
import useAxios from "hooks/useAxios";
import ItemArticleTable from "../../components/molecules/ItemArticleTable";
import { NoData, SearchStyled } from "./styled";
import { faCommentsDollar } from "@fortawesome/free-solid-svg-icons";


const Teaching = () => {
  const ListaDocencia = useAxios("GET", "/docencia/listar")
  const ListaDocenciaEspecialidades = useAxios("GET", "/DocenciaEspecialidades/listar")
  const lstUsersDocencia = useAxios("GET", `/usersSpecialties/list`)
  const lstUsersSpecialties = useAxios("GET", "/usersSpecialties/list")
  
  const [id_docencia, setDocencia] = useState();
  const [id_EspDocencia, setEspDocencia]=useState();

  

  useEffect(() => {
    if (id_docencia !== "-1") {
      lstUsersDocencia.updateURL(`/usersSpecialties/list/${id_docencia}`)
      lstUsersDocencia.initRequest()
      ListaDocenciaEspecialidades.updateURL(`/DocenciaEspecialidades/listar/${id_docencia}`)
      ListaDocenciaEspecialidades.initRequest()
    } else {
       lstUsersDocencia.updateURL(`/usersSpecialties/list`)  
      lstUsersDocencia.initRequest()
    }
    return () => { };
  }, [id_docencia])

  
  useEffect(() => {
    lstUsersDocencia.updateURL(`/usersSpecialties/list`)
    lstUsersDocencia.initRequest()
   
    return () => {};
  }, [])

  useEffect(() => {

    lstUsersDocencia.updateURL(`/usersSpecialties/list/${id_docencia}/${id_EspDocencia}`)
    lstUsersDocencia.initRequest()
    return () => { };
  }, [id_EspDocencia])


// id de docencia
  const getUsuarioDocencia = (e) => {
    e.preventDefault();
    setDocencia(e.target.value)
  }
  // id de especialidad
  const getUsuEspDoc=(e)=> {
    e.preventDefault();
    setEspDocencia(e.target.value)
    console.log("el valor del id de la especialidad es "+e.target.value);
  }

  return (
    <Fragment>
      <div >
        <Form.Select size="sm" onChange={getUsuarioDocencia}>
          <option value="-1">Seleccionar</option>
          {
            ListaDocencia.data.map(el => <option value={el.ID_DOCENCIA}>{el.DESCRIPTION_DOCEN}</option>)
          }
        </Form.Select>
        {/* <br /> */}
        <Form.Select size="sm" onChange= {getUsuEspDoc}>
          <option value="-1">Seleccionar</option>
          {
            ListaDocenciaEspecialidades.data.map(el => <option value={el.ID_ESPECIALIDAD}>{el.DESCRIPTION_ESPEC}</option>)
          }
        </Form.Select>
        
      </div>
      <SearchStyled>
        <DataTable
          noDataComponent={<NoData><span>No se encontrÃ³ ningun resultados.</span></NoData>}
          columns={[
            {
              name: "NOMBRE", cell: (el) =>
                <el className="data NOMBRE">{el.NOMBRE}</el>
            }
            ,
            {
              name: "APELLIDO PATERNO", cell: (el) =>
                <el className="data NOMBRE">{el.APE_PATERNO}</el>

            },
            {
              name: "APELLIDO MATERNO", cell: (el) =>
                <el className="data NOMBRE">{el.APE_MATERNO}</el>

            },
            {
              name: "DNI", cell: (el) =>
                <el className="data NOMBRE">{el.DNI}</el>

            },
            {
              name: "ID_DOCENCIA", cell: (el) =>
                <el className="data NOMBRE">{el.ID_DOCENCIA}</el>

            },

            {
              name: "DESCRIPTION", cell: (el) =>
                <el className="data NOMBRE">{el.DESCRIPTION_DOCEN}</el>

            },
            {
              name: "ID_ESPECIALIDAD", cell: (el) =>
                <el className="data NOMBRE">{el.ID_ESPECIALIDAD}</el>

            },
            {
              name: "DESCRIPTION", cell: (el) =>
                <el className="data NOMBRE">{el.DESCRIPTION_ESPEC}</el>

            },

          ]}
          pagination
          data={lstUsersDocencia.data}
        />
      </SearchStyled>
    </Fragment>

  );

}
export default Teaching;